(function() {
	'use strict';

	/**
	 * @ngdoc service
	 * @name home.factory:Socket
	 * 
	 * @description
	 * 
	 */
	Socket.$inject = ["$rootScope"];
	angular.module('home').factory('Socket', Socket);

	function Socket($rootScope) {
		var SocketBase = {};

		var socket = io.connect();
		SocketBase.on = function(eventName, callback) {
			socket.on(eventName, function() {
				var args = arguments; 
				$rootScope.$apply(function() {
					callback.apply(socket, args);
				});
			});
		}

		SocketBase.emit = function(eventName, data, callback) {
			socket.emit(eventName, data, function() {
				var args = arguments;
				$rootScope.$apply(function() {
					if (callback) {
						callback.apply(socket, args);
					}
				});
			})
		}

		return SocketBase;
	}
}());

(function() {
	'use strict';

	/**
	 * @ngdoc object
	 * @name home.controller:RightAsideCtrl
	 * 
	 * @description
	 * 
	 */
	RightAsideCtrl.$inject = ["$http", "$scope", "featureName"];
	angular.module('home').controller('RightAsideCtrl', RightAsideCtrl);

	function RightAsideCtrl($http, $scope, featureName) {
		var vm = this;
		vm.ctrlName = 'RightAsideCtrl';

		vm.featureName = featureName.replace(" ","_").toLowerCase();

		vm.types = [ 'css', 'id', 'binding', 'model', 'name', 'linkText', 'partialLinkText', 'tagName', 'xpath' ];

		$http.get('steps/objects?feature='+vm.featureName).then(function(res) {
			vm.pageObjects=res.data;
		},function(){
			console.log(1);
			vm.pageObjects=[]
		})

		vm.addPage = function() {
			var pageName = "New Page"
			vm.pageObjects.push({
				name:pageName,
				objects:[]
			})
		}

		vm.addNewObject = function(i) {
			vm.pageObjects[i].objects.push({
				type:'css'
			})
			
		}

		vm.saveObjects = function() {
			$http.post('steps/objects?feature='+vm.featureName, {
				objects : vm.pageObjects
			}).then(function(res) {

			})
		}

		$scope.$on('modal.closing', function() {
			vm.saveObjects();
		})
	}
}());

(function () {
  'use strict';

  /**
   * @ngdoc object
   * @name home.controller:ModalInstanceCtrl
   *
   * @description
   *
   */
  ModalInstanceCtrl.$inject = ["$scope", "$uibModalInstance"];
  angular
    .module('home')
    .controller('ModalInstanceCtrl', ModalInstanceCtrl);

  function ModalInstanceCtrl($scope, $uibModalInstance) {
    var vm = this;
    vm.ctrlName = 'ModalInstanceCtrl';
    
    $scope.ok = function () {
      $uibModalInstance.close($scope.feature.name);
    };

    $scope.cancel = function () {
      $uibModalInstance.dismiss('cancel');
    };
  }
}());

(function () {
  'use strict';

  config.$inject = ["$stateProvider"];
  angular
    .module('home')
    .config(config);

  function config($stateProvider) {
    $stateProvider
      .state('home', {
        url: '/home',
        templateUrl: 'home/home.tpl.html',
        controller: 'HomeCtrl',
        controllerAs: 'home'
      });
  }
}());

(function () {
  'use strict';

  /**
   * @ngdoc object
   * @name home.controller:HomeCtrl
   * 
   * @description
   * 
   */
  HomeCtrl.$inject = ["$http", "$log", "$window", "Socket", "toastr", "$uibModal"];
  angular.module('home').controller('HomeCtrl', HomeCtrl);

  function HomeCtrl($http, $log, $window, Socket, toastr, $uibModal) {
    var vm = this;
    vm.ctrlName = 'HomeCtrl';
    vm.showLogPanel = false;

    this.toggleModal = function () {
      this.showModal = !this.showModal;
    };

    vm.addNewScenario = function () {
      var scenario = {
        name: 'Enter scenario name',
        steps: []
      };
      vm.feature.scenarios.push(scenario);
      vm.scenarioSelected = scenario;
      vm.upload(vm.feature);
    };

    vm.config = {
      browser: 'chrome'
    };


    vm.runFeature = function (featureName) {
      vm.upload(vm.feature).then(function () {
        vm.message = '';
        vm.showReport = false;
        vm.showLogsPanel = true;
        var browser = vm.config.browser;
        $http.get('/steps/run?browser=' + browser + '&feature=' + featureName);
      });
    };
    vm.runScenario = function (scenarioName) {
      vm.upload(vm.feature).then(function () {
        vm.message = '';
        vm.showReport = false;
        vm.showLogsPanel = true;
        var browser = vm.config.browser;
        $http.get('/steps/run?browser=' + browser + '&feature=' + vm.feature.name + '&scenarioName=' + scenarioName);
      });
    };

    Socket.on('message', function (data) {
      vm.message = vm.message + data.message;
    });
    Socket.on('exit', function () {
      vm.showReport = true;
    });

    vm.addNewFeature = function () {
      var modalInstance = $uibModal.open({
        animation: true,
        templateUrl: 'myModalContent.html',
        controller: 'ModalInstanceCtrl',
        size: 'md'
      });

      modalInstance.result.then(function (name) {

        vm.features[name] = {
          name: name,
          description: 'Feature Description',
          scenarios: [{
            name: 'Scenarion Name',
            steps: []
          }]
        };

        vm.feature = vm.features[name];

        vm.upload(vm.feature);

      }, function () {
        $log.info('Modal dismissed at: ' + new Date());
      });
    };

    vm.showConf = function () {
      $uibModal.open({
        templateUrl: 'home/right-aside.tpl.html',
        controllerAs: 'rightAside',
        controller: 'RightAsideCtrl',
        placement: 'right',
        size: 'md',
        resolve: {
          featureName: function () {
            return vm.feature.name;
          }
        }
      });
    };


    $http.get('/steps').then(function (res) {
      vm.steps = res.data.steps;
      vm.features = res.data.features;
      vm.feature = vm.features[Object.keys(vm.features)[0]];
      // vm.showConf()

    });

    vm.addMoreScenario = function () {
      var scenario = {
        name: 'Enter Scenario Name',
        steps: []
      };
      // vm.feature.scenarios.push(scenario);

      vm.scenarioSelected = scenario;
      vm.upload(vm.feature);
    };

    vm.upload = function (feature) {

      return $http.post('/steps/export', {
        feature: feature
      }).then(function () {

        toastr.success('Feature saved!');
      });
    };

    vm.deleteFeature = function (feature) {
      console.log(feature);
      $http.delete('/steps/export', {
        params: {
          name: feature.name
        }
      }).then(function () {
        delete vm.features[feature.name];
        if (vm.feature.name === feature.name) {
          vm.feature = vm.features[Object.keys(vm.features)[0]];
        }
      });
    };

    vm.deleteScenario = function (scenario) {
      var x = vm.feature.scenarios.indexOf(scenario);
      vm.feature.scenarios.splice(x, 1);
      vm.upload(vm.feature);
    };
  }
}());

